import bus from '../utils/bus.js'
export default {
	onLoad() {
	},
	data() {
		return {
			selected:[
			],
			tagContent:'',
			//最近使用的标签
			recentUsed:['青春回忆','踏进校园'],
			hotTags:['球球老师捞捞我','恋爱','铲屎官','天桥底的美食','冲向饭堂']				

		}
	},
	methods:{
		//防抖
		debounce(func,delay){
			console.log(1111111)
			let timeout=null;
			if(timeout) clearTimeout(timeout)
			timeout=setTimeout(()=>{
				func.apply(this,arguments)
				console.log(22222222)
			},delay)
		},
		//搜索框添加标签
		addTag(){
			this.selected.push(this.tagContent);
			this.tagContent='';
		},
		//标签添加
		addThisTag(idx, list){
			this.selected.push(list[idx]);
		},
		//删除已添加的标签
		delThisTag(idx){
			this.selected.splice(idx,1);
		},
		addAllTag(){
			console.log(this.selected)
			this.$store.commit('tagsToPostStory',this.selected)
			// bus.$emit('tagsToPostStory',this.selected)
			uni.navigateBack({  //uni.navigateTo跳转的返回，默认1为返回上一级
				delta: 1
			});
		}
	},
}
